@import '../../utils/_scss-variables.scss';

.header {
  height: 70px;
  position: sticky;
  padding: 80px $desktop-padding 0;
  top: 0;
  background-color: $main-bg-color;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 160px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $secondary-bg-color;
  font-weight: 400;
  font-size: 40px;
  line-height: 48px;
  text-decoration: none;
  color: $main-text-color;
}

.profile {
  width: 50px;
  height: 50px;
  background-image: url(../../images/profile-icon.svg);
  background-color: transparent;
  cursor: pointer;
  transition: opacity 0.3 ease-in-out;
}

.profile:hover {
  opacity: 0.6;
}
